import { connect } from 'react-redux';
import {reduxForm, reset} from 'redux-form';

import {
  fetchDictionaryServiceKindsRequested,
  updateDictionaryServiceKindsRequested,
  createDictionaryServiceKindsRequested
} from '../../../redux/dictionaries/actions';

import { DictionaryList } from '../../../components/index';
import {validateDictionaryServiceKindsItem as validate} from '../../../util/validator';

const mapStateToProps = state => ({
  items: state.dictionaries.serviceKinds
});

const mapDispatchToProps = {
  fetchDictionaryServiceKindsRequested,
  updateDictionaryServiceKindsRequested,
  createDictionaryServiceKindsRequested,
  reset
};
export default reduxForm({
  form: 'createDictionaryServiceKinds',
  validate
})(connect(mapStateToProps, mapDispatchToProps)(DictionaryList));



