import {
  FETCH_DICTIONARY_SERVICE_KINDS_FAILED,
  FETCH_DICTIONARY_SERVICE_KINDS_REQUESTED,
  FETCH_DICTIONARY_SERVICE_KINDS_SUCCEEDED,

  UPDATE_DICTIONARY_SERVICE_KINDS_FAILED,
  UPDATE_DICTIONARY_SERVICE_KINDS_REQUESTED,
  UPDATE_DICTIONARY_SERVICE_KINDS_SUCCEEDED,

  CREATE_DICTIONARY_SERVICE_KINDS_REQUESTED,
  CREATE_DICTIONARY_SERVICE_KINDS_SUCCEEDED,
  CREATE_DICTIONARY_SERVICE_KINDS_FAILED,
} from './constants';

export const fetchDictionaryServiceKindsRequested = data => ({ type: FETCH_DICTIONARY_SERVICE_KINDS_REQUESTED, payload: data });
export const fetchDictionaryServiceKindsSucceeded = data => ({ type: FETCH_DICTIONARY_SERVICE_KINDS_SUCCEEDED, payload: data });
export const fetchDictionaryServiceKindsFailed = data => ({ type: FETCH_DICTIONARY_SERVICE_KINDS_FAILED, payload: data });

export const updateDictionaryServiceKindsRequested = data => ({ type: UPDATE_DICTIONARY_SERVICE_KINDS_REQUESTED, payload: data });
export const updateDictionaryServiceKindsSucceeded = data => ({ type: UPDATE_DICTIONARY_SERVICE_KINDS_SUCCEEDED, payload: data });
export const updateDictionaryServiceKindsFailed = data => ({ type: UPDATE_DICTIONARY_SERVICE_KINDS_FAILED, payload: data });

export const createDictionaryServiceKindsRequested = data => ({ type: CREATE_DICTIONARY_SERVICE_KINDS_REQUESTED, payload: data });
export const createDictionaryServiceKindsSucceeded = data => ({ type: CREATE_DICTIONARY_SERVICE_KINDS_SUCCEEDED, payload: data });
export const createDictionaryServiceKindsFailed = data => ({ type: CREATE_DICTIONARY_SERVICE_KINDS_FAILED, payload: data });



