import { connect } from 'react-redux';
import { reduxForm, initialize } from 'redux-form';

import { UsersRoles } from '../../components';

import { validateRole as validate } from '../../util/validator';

import {
  createRoleRequested,
  patchRoleRequested
} from '../../redux/roles/actions';

const mapStateToProps = state => ({
  roles: state.roles
});

const mapDispatchToProps = {
  createRoleRequested,
  patchRoleRequested,
  initialize
};

export default reduxForm({
  form: 'createRole',
  fields: ['role'],
  validate,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(UsersRoles));



