import React, { PureComponent } from 'react';
import { Col, Row } from 'reactstrap';

import {
  ServiceKindsFilters,
  ServiceKindsDictionary,
} from '../../containers';

class Page extends PureComponent {
  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col>
            <ServiceKindsFilters />
          </Col>
        </Row>
        <Row>
          <Col>
            <ServiceKindsDictionary />
          </Col>
        </Row>
      </div>
    );
  }
}

export default Page;



