const _validateRole = role => {
  if (!role) { return { role: 'Роль не може бути порожнім' }; }
  if (role.length < 3) { return { role: 'Роль не може бути меньше 3 символів' }; }
  return {};
};

const _validateCode = code => {
  if (!code) { return { code: 'Код не може бути порожнім' }; }
  return {};
};

const _validateName = name => {
  if (!name) { return { name: 'Назва запису не може бути порожнім' }; }
  if (name.length < 3) { return { name: 'Назва запису не може бути меньше 3 символів' }; }
  return {};
};

export const validateRole = values => ({
  ..._validateRole(values.role)
});

export const validateDictionaryServiceKindsItem = values => ({
  ..._validateCode(values.code),
  ..._validateName(values.name)
});



